.dialog-window {
  height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}

.dialog-window__head {
  width: 100%;
  background-color: #fff;
  display: flex;
  align-items: center;
  column-gap: 20px;
  flex: 1;
  height: 70px;
  padding: 0 15px;
  box-shadow: 0px 1px 4px grey;
  position: relative;
  z-index: 2;
}

.dialog-window__scroll {
  flex: 8;
  padding: 15px;
  padding-bottom: 20px;
  overflow-y: scroll;
  position: relative;
  z-index: 2;
}

.dialog-window__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  mix-blend-mode: exclusion;
  z-index: 0;
  opacity: 0.7;
  object-fit: cover;
}
