.message_wrap {
  display: flex;
  width: 100%;
  justify-content: flex-start;
}

.message_wrap.my-message {
  justify-content: flex-end;
}

.message_wrap.my-message > .message {
  background-color: rgb(36, 249, 164);
}

.message {
  background-color: #fff;
  padding: 10px 15px;
  position: relative;
  z-index: 2;
  margin-bottom: 20px;
  max-width: 45%;
  border-radius: 20px;
}

.message__addressee {
  font-size: 12px;
  color: grey; 
}